import i18n from '../../utils/i18n'
const sourceImages = [];
const base = parseInt((Math.random() * 60), 10) + 10;
for (let i = 0; i < 10; i++) {
  sourceImages.push({
    thumbnail: `https://picsum.photos/id/${base + i}/346/216`,
    source: `https://picsum.photos/id/${base + i}/1440/900`
  });
}
import Config from '../../../config/nameConfig';
const prefixCls = `${Config.prefixCls}PreviewPicture`;

export default {
  name: prefixCls,
  props: {
    /** 弹窗是否展示	 */
    show:{
      type:Boolean,
      default:()=>{
        return false
      }
    },
    /** 图片列表 */
    images: {
      type: Array,
      default: () => []
    },
    /** 默认展示第几个	 */
    defaultIndex: {
      type: Number,
      default: 0
    },
    /**
     * 弹窗属性
     */
    ModalProps: {
      type: Object,
      default: function() {
        return {
          closable: true,
          width: 500,
          height: 300,
          draggable: true,
          title: this.$t('previewPicture.picturePreview'),
          'z-index': 10001,
          'footer-hide': true
        }
      }
    },
    /**
     * 挂载dom
     */
    mountedDom: {
      type: HTMLElement,
    }
  },
  watch:{

  },
  data () {
    return {
      options: {
        inline: true,
        button: true,
        navbar: true,
        title: false,
        toolbar: true,
        tooltip: true,
        movable: true,
        zoomable: true,
        rotatable: true,
        scalable: true,
        transition: true,
        fullscreen: true,
        keyboard: true,
        url: 'data-source'
      },
    };
  },
  methods: {
    cancel () {
      /**
       * 关闭弹框
       */
      this.$emit('on-cancel');
    },
    getOffset (el) {
      el = el.getBoundingClientRect();
      return {
        left: el.left + window.scrollX,
        top: el.top + window.scrollY
      };
    },
    inited (viewer) {
      this.$viewer = viewer;
      this.$viewer.view(Number(this.defaultIndex) - 1);
      setTimeout(() => {
        // 模拟弹窗拖拽，处理fixed在transform下失效的问题
        const dom = this.mountedDom || this.$parent.$el.getElementsByClassName('ark-modal-body')[0]
        if(!dom) {
          return
        }
        const offset = this.getOffset(dom);
        const a = {
          clientX: offset.left + 0.1,
          clientY: offset.top + 0.1
        };
        this.$parent.handleMoveStart && this.$parent.handleMoveStart(a);
        this.$parent.handleMoveEnd && this.$parent.handleMoveEnd();
      }, 500);
    },
  },
  mounted(){
    // console.log('images', this.images)
  },

  beforeCreate() {
    this.$t = i18n.t.bind(i18n)
  },
};
