export default {
  name: '',

  props: {
    params: {
      type: Object,
    }
  },

  data() {
    return {
      tags: []
    }
  },

  methods: {

  },

  mounted() {
    this.params.colDef.key_group.map(item => {
      const value = this.params.data[item.col_name]; // 来源字段的值
      item.label.map((temp) => {
        if(!value) {
          return
        }
        if (temp.value == value.val) {
          this.tags.push({
            cssclass: temp.cssclass,
            description: temp.description,
            comments: temp.comments,
          })
        }
        return temp;
      });
    })
  }
}
